// export default from './npm-com-bg-message.js';
const typeMap = {
  success: 'success',
  info: 'info',
  warning: 'warning',
  error: 'error'
}
export default {
  data () {
    return {
      visible: '',
      message: '',
      duration: 3000,
      type: 'info',
      onClose: null,
      closed: false,
      verticalOffset: 20,
      iconClass: '',
      customClass: '',
      timer: null,
      dangerouslyUseHTMLString: false,
    };
  },

  mounted () {
    this.startTimer();
  },

  watch: {
    closed(newVal) {
      if (newVal) {
        this.visible = false;
      }
    }
  },

  computed: {
    typeClass() {
      return this.type && !this.iconClass
        ? `com-message__icon com-icon-${ typeMap[this.type] }`
        : '';
    },

    positionStyle() {
      return {
        'top': `${ this.verticalOffset }px`
      };
    }
  },

  props: {
    
  },
  methods: {
    handleAfterLeave() {
      this.$destroy(true);
      this.$el.parentNode.removeChild(this.$el);
    },

    clearTimer() {
      clearTimeout(this.timer);
    },

    close() {
      this.closed = true;
      if(typeof this.onClose === 'function') {
        this.onClose(this);
      }
    },

    startTimer() {
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          if (!this.closed) {
            this.close();
          }
        }, this.duration);
      }
    },
  },
};
